h1,h2{
    color: orange
}
h1{
    font-size: 20px;
}
.digimonCard{
    border: solid orange 2px;
    border-radius: 10px ;
    width: auto;
    height: auto;
    align-content: center;
    background-color:blue;
    padding: 15px;
    font-weight: 200;
    font-family: 'Digimon';
    animation: 2s;
}
.digimonCard:hover{
    cursor: pointer;
    transform: scale(1.1);
}
img{
 border: solid 3px orange;
 border-radius: 10px;
}
@font-face {
    font-family: 'Digimon';
    src: url('./DigimonBasic.ttf');
}
