.main-box{
margin: 0 auto;
display: flex;
flex-wrap: wrap;
justify-content: center;
font-family: 'Digimon';
}
.header-box{
    text-align: center;
    border: solid black 3px;
    width: auto;
    height: auto;
    margin: 1% auto;
    padding: 15px;
    background-color: blue;
}
.header-box h1{
    font-size: 60px;
    font-family: 'Digimon';
}
.header-box input{
    border: solid black 2px;
    background-color:aqua;
    width: 300px;
    height: 40px;
    font-family: 'Digimon';
    color: black;
}
.header-box input:hover{
    transform: scale(1.1);
}